
import React, { Component } from 'react';
import {
  Button,
  ButtonGroup,
  Card,
  CardBody,
  CardHeader,
  Col,
  Collapse,
  Fade,
  Form,
  FormGroup,
  Input,
  InputGroup,
  Label,
  Row,
} from 'reactstrap';
import { Redirect } from 'react-router-dom';
import Cookies from 'universal-cookie';
const cookies = new Cookies();

class CreateQuestion extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleFade = this.toggleFade.bind(this);
    this.selectPriority = this.selectPriority.bind(this);
    this.updateInputValue = this.updateInputValue.bind(this);
    this.publish = this.publish.bind(this);
    this.state = {
      collapse: true,
      fadeIn: true,
      timeout: 300,
      priority: 'Low',
      title: '',
      description: '',
      isLoggedIn: false,
      isAdmin: false,
    };
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggleFade() {
    this.setState((prevState) => { return { fadeIn: !prevState } });
  }

  selectPriority(prio) {
    this.setState({ priority: prio });
  }

  publish() {
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ title: this.state.title, description: this.state.description, priority: this.state.priority })
    };
    fetch('https://missionaustralia-back.herokuapp.com/addQuestion', requestOptions)
      .then(response => response.json())
      .then(resData => {
        console.log(resData)
        if (resData.response === 200) {
          alert(resData.msg);
        } else {
          alert(resData.msg);
        }
      });
  }

  componentWillMount() {
    if (cookies.get('token') !== undefined && cookies.get('token') !== null) {
      this.setState({
        isLoggedIn: true
      });
    }

    if (cookies.get('admin') !== 'false'){
      this.setState({
        isAdmin: true
      });
    } else {
      this.setState({
        isAdmin: false
      });
    }
  }

  updateInputValue(evt, val) {
    if (val === 1) {
      this.setState({
        title: evt.target.value
      });
    } else if (val === 2) {
      this.setState({
        description: evt.target.value
      })
    }
  }

  render() {
    if (this.state.isLoggedIn) {
      return (
        <div className="animated fadeIn">
          <Row>
            <Col xs="12">
              <Fade timeout={this.state.timeout} in={this.state.fadeIn}>
                <Card>
                  <CardHeader>
                    <i className="fa fa-edit"></i>Create Question
                  <div className="card-header-actions">
                      <Button color="link" className="card-header-action btn-setting"><i className="icon-settings"></i></Button>
                      <Button color="link" className="card-header-action btn-minimize" data-target="#collapseExample" onClick={this.toggle}><i className="icon-arrow-up"></i></Button>
                      <Button color="link" className="card-header-action btn-close" onClick={this.toggleFade}><i className="icon-close"></i></Button>
                    </div>
                  </CardHeader>
                  { this.state.isAdmin === false ?
                  <Collapse isOpen={this.state.collapse} id="collapseExample">
                    <CardBody>
                      <Form className="form-horizontal">
                        <FormGroup>
                          <Label htmlFor="appendedInput">Title</Label>
                          <div className="controls">
                            <InputGroup>
                              <Input id="appendedInput" size="16" type="text" name="title" value={this.state.title} onChange={evt => this.updateInputValue(evt, 1)} />
                            </InputGroup>
                          </div>
                        </FormGroup>
                        <FormGroup row>
                          <Col md="3">
                            <Label htmlFor="description">Description</Label>
                          </Col>
                          <Col xs="12" md="9">
                            <Input type="textarea" name="description" id="description" rows="9"
                              placeholder="Question Description..." value={this.state.description} onChange={evt => this.updateInputValue(evt, 2)} />
                          </Col>
                        </FormGroup>
                        <FormGroup>
                          <Col md="3">
                            <Label htmlFor="textarea-input">Priority : <b>{this.state.priority}</b></Label>
                          </Col>
                          <Col xs="12" md="9">
                            <ButtonGroup>
                              <Button type="button" className="btn-success" onClick={() => this.selectPriority('Low')}>Low</Button>
                              <Button type="button" className="btn-info" onClick={() => this.selectPriority('Medium')}>Medium</Button>
                              <Button type="button" className="btn-danger" onClick={() => this.selectPriority('High')}>High</Button>
                            </ButtonGroup>
                          </Col>
                        </FormGroup>
                        <div className="form-actions">
                          <Button type="button" color="primary" onClick={() => this.publish()}>Publish Question</Button>
                        </div>
                      </Form>
                    </CardBody>
                  </Collapse>
                  : 
                  <div style={{textAlign: "center"}}>Admin users cannot post questions</div> }
                </Card>
              </Fade>
            </Col>
          </Row>
        </div>
      );
    } else {
      return <Redirect to='/login' />
    }
  }
}

export default CreateQuestion;
